.container__navbar {
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  justify-content: space-between;
  position: fixed;
  padding: 0 0 10px 0;
  margin: 0px auto;
  right: 0;
  left: 0;
  top: 0;
}

.logo-navbar {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-left: 30px;
}
.logo-navbar img {
  max-width: 10%;
}
.logo-navbar img:nth-child(2) {
  max-width: 30%;
  margin-top: 20px;
  margin-left: 20px;
}

.navbar-menu {
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin-right: 10rem;
  margin-top: 20px;
}
.navbar-menu li {
  margin: 0 15px;
  font-size: 150%;
  font-weight: bold;
}
.navbar-menu li a {
  color: black;
}
